aAAc,CACd,gBAAiB,CACjB,0BAA2B,CAC3B,QAAS,CA9IjB,kBAiJY,UAAW,CACX,gBAAiB,CACjB,iBAAkB,CAClB,WAAY,CACZ,cAAe,CACf,0BAA2B,CAtJvC,+DAyJgB,gBAAiB,CACjB,iBAAkB,CACrB","sourcesContent":["@import 'variaveis';\r\n@import 'mixins';\r\n\r\n.overlay {\r\n    display: block;\r\n    width: 100%;\r\n    min-height: 0;\r\n    background-color: $preto;\r\n    opacity: .4;\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    z-index: 99;\r\n    transition: min-height .2s ease-in-out;\r\n\r\n    &.aberto {\r\n        min-height: 100vh;\r\n    }\r\n}\r\n\r\n.editaNota {\r\n    background-color: $branco;\r\n    position: fixed;\r\n    top: -50%;\r\n    left: 50%;\r\n    transform: translate(-50%, -50%);\r\n    z-index: 100;\r\n    width: calc(100vw - 48px);\r\n    max-width: 600px;\r\n    padding: 30px 25px;\r\n    border-radius: 15px;\r\n    transition: top .4s ease-in-out;\r\n\r\n    &.aberto {\r\n        top: 50%;\r\n    }\r\n\r\n    input {\r\n        width: 100%;\r\n        font-weight: 700;\r\n    }\r\n\r\n    textarea {\r\n        outline: none;\r\n        border: 0;\r\n        overflow-x: hidden;\r\n        overflow-y: auto;\r\n        max-height: 200px;\r\n        height: 20px;\r\n        width: 100%;\r\n        resize: none;\r\n        font-size: 1rem;\r\n        appearance: none;\r\n    }\r\n\r\n    span {\r\n        display: block;\r\n        background-color: lighten($cinza, 15%);\r\n        height: 1px;\r\n        width: 100%;\r\n        margin: 15px 0;\r\n    }\r\n\r\n    section {\r\n        display: flex;\r\n        justify-content: flex-end;\r\n        flex-wrap: wrap;\r\n    }\r\n\r\n    button {\r\n        width: auto;\r\n        height: auto;\r\n        background-color: $cor-principal;\r\n        color: $branco;\r\n        padding: 10px 20px;\r\n        @include el3d();\r\n\r\n        &::after {\r\n            @include pseudo(100%, darken($cor-principal, 15%), 5px);\r\n        }\r\n    }\r\n\r\n    .btnCancelar {\r\n        color: $cinza;\r\n        background-color: transparent;\r\n\r\n        &:active {\r\n            transform: none;\r\n        }\r\n\r\n        &::after {\r\n            background-color: transparent;\r\n        }\r\n    }\r\n}\r\n\r\n.confirmaExcluir {\r\n    width: 80vw;\r\n    max-width: 320px;\r\n    position: fixed;\r\n    left: 50%;\r\n    top: -50%;\r\n    transform: translate(-50%, -50%);\r\n    background-color: $branco;\r\n    border-radius: 15px;\r\n    z-index: 100;\r\n    padding: 30px 40px;\r\n    @include flex();\r\n    flex-direction: column;\r\n    transition: top .4s ease-in-out;\r\n\r\n    &.aberto {\r\n        top: 50%;\r\n    }\r\n\r\n    p {\r\n        margin-bottom: 1rem;\r\n        font-weight: 700;\r\n        text-align: center;\r\n    }\r\n\r\n    button.btnCancelar {\r\n        width: auto;\r\n        height: auto;\r\n        background-color: $cor-principal;\r\n        color: $branco;\r\n        padding: 10px 20px;\r\n        @include el3d();\r\n\r\n        &::after {\r\n            @include pseudo(100%, darken($cor-principal, 15%), 5px);\r\n        }\r\n    }\r\n}\r\n\r\n.janelaNotificacao {\r\n    color: $branco;\r\n    text-align: center;\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    z-index: 1000;\r\n\r\n    button {\r\n        height: 50px;\r\n        width: 50px;\r\n\r\n        @include media-desktop {\r\n            position: absolute;\r\n            right: 0;\r\n        }\r\n\r\n        img {\r\n            width: 25px;\r\n        }\r\n    }\r\n\r\n    span {\r\n        position: absolute;\r\n        width: 100%;\r\n        height: 3px;\r\n        background: $branco;\r\n        bottom: 0;\r\n        left: 0;\r\n        opacity: .3;\r\n        transition: width 1s linear;\r\n    }\r\n\r\n    .notificacao {\r\n        width: 100%;\r\n        padding: 1rem;\r\n        background-color: mix($preto, transparent, 90%);\r\n        transition: .3s ease;\r\n        @include flex();\r\n        flex-wrap: wrap;\r\n        position: relative;\r\n        margin-bottom: 1px;\r\n        animation: fade-in .5s ease;\r\n        \r\n        @keyframes fade-in {\r\n            from {\r\n                transform: translateY(5px);\r\n                opacity: .5;\r\n            }\r\n\r\n            to {\r\n                transform: translateY(0);\r\n                opacity: 1;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n.visualizacao {\r\n    position: fixed;\r\n    left: 50%;\r\n    top: -50%;\r\n    transform: translate(-50%, -50%);\r\n    padding: 30px 25px;\r\n    border-radius: 15px;\r\n    transition: top .4s ease-in-out;\r\n    z-index: 100;\r\n    background-color: $branco;\r\n    display: flex;\r\n    flex-direction: column;\r\n    width: calc(100vw - 48px);\r\n    max-width: 600px;\r\n    \r\n    .title-visu {\r\n        color: $cor-principal;\r\n    }\r\n\r\n    .content-visu {\r\n        overflow-x: hidden;\r\n        overflow-y: auto;\r\n        max-height: 200px;\r\n        white-space: pre-line;\r\n    }\r\n\r\n    &.aberto {\r\n        top: 50%;\r\n    }\r\n\r\n    & > span {\r\n        margin: 1rem 0;\r\n        display: block;\r\n        height: 1px;\r\n        width: 100%;\r\n        background-color: lighten($cinza, 15%);\r\n    }\r\n\r\n    button {\r\n        margin: 0 auto;\r\n        width: auto;\r\n        height: auto;\r\n        background-color: $preto;\r\n        color: $branco;\r\n        padding: 10px 30px;\r\n        max-width: 120px;\r\n        @include el3d();\r\n\r\n        &::after {\r\n            @include pseudo(100%, $preto, 5px);\r\n        }\r\n    }\r\n}\r\n\r\n.opcoes, .ticket_acesso {\r\n    position: fixed;\r\n    left: 50%;\r\n    top: -50%;\r\n    transform: translate(-50%, -50%);\r\n    background-color: $branco;\r\n    z-index: 100;\r\n    text-align: center;\r\n    @include flex();\r\n    flex-direction: column;\r\n    font-size: 1rem;\r\n    color: $preto;\r\n    border-radius: 15px;\r\n    transition: top .4s ease-in-out;\r\n\r\n    &.aberto {\r\n        top: 50%;\r\n    }\r\n\r\n    & > * { \r\n        padding: 5px;\r\n    }\r\n\r\n    & > :nth-child(n+2) {\r\n        margin-top: 5px;\r\n    }\r\n\r\n    a {\r\n        color: $cinza;\r\n        @include flex();\r\n    }\r\n\r\n    img {\r\n        margin-left: 5px;\r\n    }\r\n\r\n    & > button.fecharBalao,\r\n    button {\r\n        width: auto;\r\n        height: auto;\r\n        background-color: $preto;\r\n        color: $branco;\r\n        padding: 10px 30px;\r\n        margin: 15px 0 30px 0;\r\n        @include el3d();\r\n\r\n        &::after {\r\n            @include pseudo(100%, $preto, 5px);\r\n        }\r\n    }\r\n\r\n    & > span {\r\n        border-radius: 13px 13px 0 0;\r\n        font-weight: 600;\r\n        padding: 10px 100px;\r\n        background-color: $cor-principal;\r\n        color: $branco;\r\n        width: 100%;\r\n        margin-bottom: 10px !important;\r\n    }\r\n}\r\n\r\n.ticket_acesso {\r\n    width: 80vw;\r\n    max-width: 330px;\r\n\r\n    section {\r\n        position: relative;\r\n        margin-bottom: 1rem;\r\n    }\r\n\r\n    p {\r\n        max-width: 250px;\r\n        font-weight: 600;\r\n        margin-bottom: .5rem;\r\n        color: lighten($preto, 30%);\r\n    }\r\n\r\n    span {\r\n        padding: 10px 0;\r\n    }\r\n\r\n    form {\r\n        padding: 10px 30px;\r\n    }\r\n\r\n    input {\r\n        border: 1px solid $preto;\r\n        width: 100%;\r\n        max-width: 250px;\r\n        font-size: 1.8rem;\r\n        font-weight: 700;\r\n        text-align: center;\r\n        padding: .5rem;\r\n        letter-spacing: 12px;\r\n        border-radius: 7px;\r\n        color: black;\r\n    }\r\n\r\n    label {\r\n        position: absolute;\r\n        display: initial;\r\n        left: 8px;\r\n        top: 0;\r\n        transform: translateY(-50%);\r\n        padding: 0 .3rem;\r\n        margin: 0;\r\n        background-color: $branco;\r\n        font-weight: 700;\r\n        font-size: .8rem;\r\n    }\r\n\r\n    button {\r\n        margin-bottom: 0;\r\n    }\r\n\r\n    button.btnEntrar {\r\n        padding: 10px 20px;\r\n        background-color: $branco;\r\n        color: $preto;\r\n        margin-right: 5px;\r\n        border: 1px solid darken($branco, 10%);\r\n\r\n        &::after {\r\n            @include pseudo(100%, $branco, 5px);\r\n        }\r\n    }\r\n\r\n    button.btnRegistrar {\r\n        padding: 10px 20px;\r\n        margin-left: 5px;\r\n        margin-bottom: 5px;\r\n    }\r\n\r\n    a {\r\n        margin-bottom: 20px;\r\n    }\r\n\r\n    .code-user {\r\n        margin-top: 10px;\r\n        font-size: 2rem;\r\n        letter-spacing: 12px;\r\n        color: $cor-principal;\r\n        margin-left: 12px;\r\n    }\r\n}\r\n\r\n.espiral {\r\n    position: relative;\r\n    top: -11px;\r\n    height: 25px;\r\n    width: 100%;\r\n    background-image: url('../img/spiral.svg');\r\n}\r\n\r\n.entradaDados {\r\n    background-color: $cor-principal;\r\n    color: $branco;\r\n    font-weight: 600;\r\n    padding-bottom: 16px;\r\n    border-radius: 0 0 15px 15px;\r\n\r\n    input, textarea {\r\n        height: 45px;\r\n        padding: 0 1rem;\r\n    }\r\n\r\n    button {\r\n        background-color: $preto;\r\n        width: 45px;\r\n        @include el3d();\r\n\r\n            &::after {\r\n                @include pseudo(100%, $preto, 0 0 5px 0);\r\n            }\r\n\r\n            img {\r\n                vertical-align: middle;\r\n            }\r\n    }\r\n\r\n    .campoAddNota {\r\n        margin: .5rem 0;\r\n\r\n        & > form {\r\n            display: flex;\r\n            @include el3d();\r\n\r\n            &::after {\r\n                @include pseudo(calc(100% - 45px), $branco, 0 0 0 5px);\r\n                height: 30px;\r\n            }\r\n\r\n            &.ativo::after {\r\n                border-radius: 0 0 5px 5px;\r\n            }\r\n\r\n            section {\r\n                display: flex;\r\n                flex-direction: column;\r\n                border-radius: 5px 0 0 5px;\r\n                width: calc(100% - 45px);\r\n            }\r\n\r\n            input {\r\n                flex-grow: 1;\r\n                font-weight: bold;\r\n                border-radius: 5px 0 0 0;\r\n                max-height: 0;\r\n            }\r\n\r\n            &.ativo input {\r\n                max-height: unset;\r\n            }\r\n\r\n            &.ativo textarea {\r\n                border-radius: 0 0 5px 5px;\r\n                padding: 12px 1rem;\r\n                line-height: 20px;\r\n            }\r\n\r\n            textarea {\r\n                border-radius: 5px 0 0 5px;\r\n                line-height: 45px;\r\n                flex-grow: 1;\r\n                resize: none;\r\n                font-weight: 600;\r\n            }\r\n\r\n            span {\r\n                display: block;\r\n                background-color: lighten($cinza, 15%);\r\n                height: 1px;\r\n                position: absolute;\r\n                top: 45px;\r\n                width: calc(100% - 2rem - 45px);\r\n                margin-left: 1rem;\r\n            }\r\n        }\r\n\r\n        button {\r\n            max-height: 45px;\r\n            border-radius: 0 5px 5px 0;\r\n        }\r\n    }\r\n\r\n    .mostrarCampoOpc {\r\n        @include flex();\r\n        position: relative;\r\n        padding-top: 15px;\r\n        \r\n        * {cursor: pointer;}\r\n\r\n        label {\r\n            @include flex();\r\n            margin-bottom: 10px;\r\n            padding-right: 0;\r\n            flex-grow: 1;\r\n        }\r\n\r\n        .linha {\r\n            margin-top: 2px;\r\n            margin-left: 10px;\r\n            display: block;\r\n            height: 1px;\r\n            width: 10px;\r\n            background-color: $branco;\r\n            opacity: .5;\r\n            flex-grow: 1;\r\n        }\r\n\r\n        input {\r\n            opacity: 0;\r\n\r\n            &:checked + span {\r\n                &::after { \r\n                